.tabs {
	width: 750px;
	height: 42.6rem;
	box-shadow: 0.3rem 0.3rem 0.6rem #000;

	@media (max-width: 1725px) {
		width: 600px;
		height: 45rem;
	}
	@media (max-width: 1400px) {
		width: 500px;
		height: 50rem;
	}
	@media (max-width: 1200px) {
		width: 450px;
		height: 65rem;
	}
	@media (max-width: 1100px) {
		width: 400px;
		height: 70rem;
	}
	@media (max-width: 950px) {
		width: 100%;
		margin-bottom: 10rem;
	}
}

.tabs input {
	position: absolute;
	z-index: z(tabs__input);
	width: 120px;
	height: 40px;
	visibility: hidden;
	opacity: 0;

	@media (max-width: 1725px) {
		width: 100px;
	}
	@media (max-width: 1400px) {
		width: 70px;
	}
	@media (max-width: 1100px) {
		width: 65px;
	}
	@media (max-width: 500px) {
		width: 60px;
	}
	@media (max-width: 430px) {
		width: 50px;
	}
}
.tabs input#tab-2{
	left: 120px;

	@media (max-width: 1725px) {
		left: 100px;
	}
	@media (max-width: 1400px) {
		left: 70px;
	}
	@media (max-width: 1100px) {
		left: 65px;
	}
	@media (max-width: 500px) {
		left: 60px;
	}
	@media (max-width: 430px) {
		left: 50px;
	}
}
.tabs input#tab-3{
	left: 240px;

	@media (max-width: 1725px) {
		left: 200px;
	}
	@media (max-width: 1400px) {
		left: 140px;
	}
	@media (max-width: 1100px) {
		left: 130px;
	}
	@media (max-width: 500px) {
		left: 120px;
	}
	@media (max-width: 430px) {
		left: 100px;
	}
}
.tabs input#tab-4{
	left: 360px;

	@media (max-width: 1725px) {
		left: 300px;
	}
	@media (max-width: 1400px) {
		left: 210px;
	}
	@media (max-width: 1100px) {
		left: 195px;
	}
	@media (max-width: 500px) {
		left: 180px;
	}
	@media (max-width: 430px) {
		left: 150px;
	}
}
.tabs input#tab-5{
	left: 480px;

	@media (max-width: 1725px) {
		left: 400px;
	}
	@media (max-width: 1400px) {
		left: 280px;
	}
	@media (max-width: 1100px) {
		left: 260px;
	}
	@media (max-width: 500px) {
		left: 240px;
	}
	@media (max-width: 430px) {
		left: 200px;
	}
}
.tabs input#tab-6{
	left: 600px;

	@media (max-width: 1725px) {
		left: 500px;
	}
	@media (max-width: 1400px) {
		left: 350px;
	}
	@media (max-width: 1100px) {
		left: 325px;
	}
	@media (max-width: 500px) {
		left: 300px;
	}
	@media (max-width: 430px) {
		left: 250px;
	}
}

.tabs label {
	background: $blue-gradient-1;
	font-size: 15px;
	line-height: 40px;
	height: 40px;
    position: relative;
    top: -40px;
	padding: 0 20px;
    float: left;
	display: block;
	width: 120px;
	color: $white;
	letter-spacing: 1px;
	text-transform: uppercase;
	font-weight: bold;
	text-align: center;
	text-shadow: 1px 1px 1px rgba(255,255,255,0.3);
    border-radius: 3px 3px 0 0;
	box-shadow: 2px 0 2px rgba(0,0,0,0.1), -2px 0 2px rgba(0,0,0,0.1);
	cursor: pointer;
	
	
	@media (max-width: 1725px) {
		width: 100px;
	}
	@media (max-width: 1400px) {
		width: 70px;
		font-size: 12px;
		padding: 0;
	}
	@media (max-width: 1100px) {
		width: 65px;
	}
	@media (max-width: 500px) {
		width : 60px;
		font-size: 11px;
	}
	@media (max-width: 430px) {
		width : 55px;
		font-size: 10.5px;
	}
	@media (max-width: 400px) {
		width : 50px;
		font-size: 10.0px;
	}
}

.tabs input:hover + label {
	background: $blue-gradient-2;
}

.tabs label:first-of-type {
  z-index: z(tabs-label-1);
  box-shadow: 2px 0 2px rgba(0,0,0,0.1);
}

.tabs-label-2 {
  z-index: z(tabs-label-2);
}

.tabs-label-3 {
  z-index: z(tabs-label-3);
}

.tabs-label-4 {
  z-index: z(tabs-label-4);
}

.tabs-label-5 {
  z-index: z(tabs-label-5);
}

.tabs-label-6 {
  z-index: z(tabs-label-6);
}

.tabs label:after {
  content: '';
	background: #fff;
	position: absolute;
	bottom: -2px;
	left: 0;
	width: 100%;
	height: 2px;
	display: block;
}

.tabs input:checked + label {
  background: #fff;
	z-index: 6;
	color: $black;
}

.content {
    background: #fff;
	position: relative;
    width: 100%;
	height: 100%;
    z-index: 5;
    bottom: 0;
    box-shadow: 0 -2px 3px -2px rgba(0,0,0,0.2), 0 2px 2px rgba(0,0,0,0.1);
	border-radius: 0 3px 3px 3px;
	vertical-align: text-bottom;
}

.content div {
  position: absolute;
	top: 0;
	left: 0;
	padding: 1rem 4rem;
	z-index: 1;
  opacity: 0;
  transition: all linear 0.1s;

  @media (max-width: 1200px) {
		padding: 1rem 2rem;
	}

	@media (max-width: 450px) {
		padding: 1rem 1rem;
	}
}

.content div h2,
.content div h3{
	color: $black;
}
.content div p {
	font-size: 15px;
	line-height: 22px;
	text-align: left;
	margin: 0;
	color: $grey-5;
	padding-left: 15px;
	font-family: 'gorditaregular', serif;
	border-left: 8px solid $blue-1;
	margin-bottom: 3rem;
}

.content div p:first-child {
	margin-top: 1rem;
}

.tabs input.tab-selector-1:checked ~ .content .content-1,
.tabs input.tab-selector-2:checked ~ .content .content-2,
.tabs input.tab-selector-3:checked ~ .content .content-3,
.tabs input.tab-selector-4:checked ~ .content .content-4,
.tabs input.tab-selector-5:checked ~ .content .content-5,
.tabs input.tab-selector-6:checked ~ .content .content-6 {
    z-index: 100;
    opacity: 1;
    transition: all ease-out 0.2s 0.1s;
}

// .clear-shadow {
// 	height: 100%;
// }

.content-1, .content-2, .content-3,
.content-4, .content-5, .content-6 {
	height: 100%;
	overflow-y: scroll;
}