.dots-loader {
  position: absolute;
  left: 50%;
  top: 50%;
  bottom: 50%;
  right: 50%;
  transform: translateX(-7rem);
}

.dots-loader:not(:required) {
  opacity: 1;
  overflow: hidden;
  position: absolute;
  left: 50%;
  top: 50%;
  text-indent: -9999px;
  display: inline-block;
  width: 8px;
  height: 8px;
  background: transparent;
  border-radius: 100%;
  box-shadow: $blue-1 -14px -14px 0 7px, $blue-2 14px -14px 0 7px, $blue-3 14px 14px 0 7px, $blue-4 -14px 14px 0 7px;
  animation: dots-loader 5s infinite ease-in-out;
  transform-origin: 50% 50%;
  transform: scale(1);
  transition: .3s all;
}